.nav_bar {
   background: #131313;
    
      height: 60px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1.2rem;
      position: sticky;
      top: 0;
      z-index: 999;
      cursor: pointer;
    }
    .navbar_container {
      display: flex;
      justify-content: space-between;
      height: 80px;
      z-index: 1;
      width: 100%;
      max-width: 1300px;
      margin: 0 auto;
      padding: 0 50px;
    }
    #navbar_logo {
      background: #fff;
     
    
      -webkit-text-fill-color: transparent;
      -moz-text-fill-color: transparent;
      display: flex;
      align-items: center;
      cursor: pointer;
      text-decoration: none;
      font-size: 2rem;
    }
    .navbar_menu {
      display: flex;
      align-items: center;
      list-style: none;
    }
    .navbar_item {
      height: 80px;
    }
    .navbar_links {
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 125px;
      text-decoration: none;
      height: 100%;
      transition: all 0.3s ease;
    }
    .navbar_btn {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0 1rem;
      width: 100%;
    }
    .button {
      display: flex;
      justify-content: center;
      align-items: center;
      text-decoration: none;
      padding: 10px 20px;
      height: 100%;
      width: 100%;
      border: none;
      outline: none;
      border-radius: 4px;
    background: #232526;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #414345, #232526);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
      color: #fff;
      transition: all 0.3s ease;
    }
    .navbar__links:hover {
      color: #9518fc;
      transition: all 0.3s ease;
    }
    .button:hover{
         color: #9518fc;
      transition: all 0.3s ease;
    
    }
    .body{
     
        background-repeat: no-repeat;
        height: 800px;
        width: 1200px;
    }
    .image{
      width: 40px;
      height: 40px;
      background-image: url("../images/LogoMakr-4gZiBb.png");
    }