.sms-form {
  display: flex;
  display: grid;
  justify-content: center;
  align-items: center;
    text-align: left;
    padding: 1em;
  }
  .sms-form label {
    display: block;
  }
  .sms-form input,
  .sms-form textarea {
    font-size: 1em;
    width: 400px;
    box-sizing: border-box;
  }
  .sms-form div {
    margin-bottom: 0.5em;
  }
  .sms-form button {
    font-size: 1em;
    width: 400px;
  }
  .sms-form.error {
    outline: 2px solid #f00;
  }
  .sms-form textarea{
    height: 200px;
  }